body {
  margin: 0;
  padding: 0;
  /*background-color: #dcdfe4;*/
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
	background-color: #282c34;
	height: 80px;
  	/*min-height: 100vh;*/
  	display: flex;
  	flex-direction: column;
  	align-items: center;
  	justify-content: center;
  	font-size: calc(10px + 2vmin);
  	color: white;
  	border-bottom: 2px solid grey;
  	border-bottom-width: thin;
}

.Header p {
  	font-size: 140%;
  	font-weight: bold;
}

.Header p span:nth-child(1) {
	color: red;
}
.Header p span:nth-child(2) {
	color: white;
}
.topnav {
	display: flex;
	/*flex-direction: row;*/
    flex-wrap: wrap;
	justify-content: space-between;	
	margin-bottom: 2%;
}

.topnav > * {
    min-width: 100px;
	padding: 12px 20px 12px 40px;
}
.SearchUser {
    
}

input[type=text] {
    width: 100%;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    background-color: white;
    background-image: url(data:image/png;base64,);
    background-position: 10px 10px; 
    background-repeat: no-repeat;
    padding: 12px 20px 12px 40px;
    -webkit-transition: width 0.4s ease-in-out;
    transition: width 0.4s ease-in-out;
}

/*input[type=text]:focus {
    width: 50%;
}*/

.dropdown {
    position: relative;
    display: inline-block;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    padding: 0;
}

.dropdown-content li {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    /*width: 100%;*/
    font-size: 100%;

    background-color: Transparent;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    outline: none;
}

.dropdown-content li:hover {
    background-color: #f1f1f1
}

.dropdown .dropdown-content.opening {
    display: block;
    
}
.RatingItem {
	/*margin-bottom: 2%;*/
    word-break: break-word;   
    word-wrap: break-word;   
    overflow-wrap: break-word;   

    margin: 0.5em;
    padding: 0.5em;
    /*width: 400px;*/
    cursor: pointer;
    background: transparent;
    padding: 1rem 1rem;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
    color: #41403E;
    outline: none;
    box-shadow: 20px 38px 34px -26px hsla(0,0%,0%,.2);
    border-radius: 255px 15px 225px 15px/15px 225px 15px 255px;
    border: solid 2px #41403E;

    margin-bottom: 2%;
}

.RatingItem > * {
    margin-bottom: 2%;
}

.RatingItem .title {
    text-align: left;
    width: 100%;
    font-size: calc(10px + 2vmin);
    font-weight: bold;
    color: #CE8686;
}

.RatingItem:hover {
	background: rgb(240, 240, 240);
    box-shadow: 2px 8px 4px -6px hsla(0,0%,0%,.3);
}

.RatingItem .winner {
    /*height: 100px;*/
    display: flex;
    flex-direction: row;
    align-items: center;
}

.RatingItem .winner .winner-name-box {
    width: 50%;
    text-align: center;
}
.RatingItem .winner .winner-place-box,
.RatingItem .winner .winner-scores-box {  
    width: 25%;
    position: relative;
    min-height: 1px;
    padding-left: 20px;
    padding-right: 20px;
    text-align: center;
}

.RatingItem .winner .winner-circle {
    width: 100px;
    height: 0;
    color: #1a1a1a;
    padding-bottom: calc(120px - 1.4rem);
    border-radius: 50%;
    border: .3rem solid #1a1a1a;
    position: relative;
}

.RatingItem .winner .winner-scores-box .winner-circle {
    border-radius: 10%;
    width: 70%;
    min-width: 100px;
    border: .1rem solid #1a1a1a;
}

.RatingItem .winner .winner-name-box span {   
    font-size: 3rem;
    font-weight: 700;
}

.RatingItem .winner .winner-place-box span,
.RatingItem .winner .winner-scores-box span {
    display: block;
    width: 100%;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    font-size: 3rem;
    font-weight: 700;
}

.RatingItem .winner .winner-scores-box span {
    font-size: 2rem;
    font-weight: 500;
}

