.Ratings {
	font-family: Tahoma, Geneva, sans-serif;
	width: 60%;
	margin: 0 auto;
	display: flex;
	flex-direction: column;	
}

.Ratings .selected-user {
	position: relative;
    display: inline-block;
    margin-top: 10px;
}

.Ratings .selected-user .selected-user-btn {
    background-color: #f44336;
    color: white;
    padding: 0 8px;
    font-size: 16px;
    border: none;
    cursor: pointer;
}

.Ratings .selected-user .closebtn {
    text-decoration: none;
    font-size: 36px;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    color: #bebebe;
    transition: 0.3s;
    background-color: Transparent;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    outline: none;
    text-decoration: none;
}

.Ratings .selected-user .closebtn:hover,
.Ratings .selected-user .closebtn:focus {
	color: #f1f1f1;
}
.Overlay {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0, 0.9);
    overflow-x: hidden;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.overlay-content {
    position: relative;
    top: 15%;
    width: 100%;
    text-align: center;
    margin: 20px 0;
}

.Overlay div, 
.Overlay button {
    padding: 8px;
    text-decoration: none;
    font-size: 36px;
    color: #818181;
    display: block;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.Overlay button {
    background-color: Transparent;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    outline: none;
}

.Overlay button:hover, 
.Overlay button:focus {
    color: #f1f1f1;
}

.Overlay .closebtn {
    position: absolute;
    top: 20px;
    right: 45px;
    font-size: 60px;
}
.App {
	text-align: center;
}
